<script>
export default {
  el: '#app',
  data() {
    return {
      username: '',
      userActive: false,
      tiles: [],
      selectedTile: null,
      participants: ['张三', '李四', '王五', '赵六', '钱七', '孙八', '周九', '吴十'],
      totalTiles: 800
    }
  },
  computed: {
      litTilesCount() {
          return this.tiles.filter(tile => tile.lit).length;
      },
      participantsCount() {
          return this.participants.length;
      },
      completionPercentage() {
          return Math.round((this.litTilesCount / this.totalTiles) * 100);
      },
      allLit() {
          return this.litTilesCount === this.totalTiles;
      }
  },
  created() {
      this.initializeTiles();
  },
  methods: {
      initializeTiles() {
          // 创建8x8网格的方块
          for (let row = 0; row < 25; row++) {
              for (let col = 0; col < 32; col++) {
                  // 随机点亮一些方块用于演示
                  const lit = Math.random() > 0.7;
                  const user = lit ? this.participants[Math.floor(Math.random() * this.participants.length)] : '';

                  this.tiles.push({
                      id: row * 8 + col,
                      row: row + 1,
                      col: col + 1,
                      lit: lit,
                      user: user
                  });
              }
          }
      },
      activateUser() {
          if (this.username.trim() === '') {
              alert('请输入您的姓名');
              return;
          }

          this.userActive = true;
          this.participants.push(this.username);
      },
      selectTile(tile) {
          if (!this.userActive) {
              alert('请先激活您的账号');
              return;
          }

          if (tile.lit) {
              alert('这个方块已经被点亮了');
              return;
          }

          this.selectedTile = tile;
      },
      lightUpTile() {
          if (!this.selectedTile) {
              alert('请先选择一个方块');
              return;
          }

          // 在实际应用中，这里会发送请求到后端
          this.selectedTile.lit = true;
          this.selectedTile.user = this.username;

          // 在实际应用中，这里会广播更新给所有用户

          alert(`成功点亮方块 ${this.selectedTile.row}-${this.selectedTile.col}`);
          this.selectedTile = null;
      }
  }
}
</script>